.wrapper {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 20px 30px 20px;
}

.contact_info {
    display: flex;
    justify-content: center;
    gap: 20px;
    transform: translateY(15%);
    padding: 0 20px;
}

.contact_item {
    width: 200px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    padding: 20px;
    border-radius: 15px;
    box-shadow: 1px 1px 8px 3px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in;
}

.contact_item:hover {
    transform: scale(1.05);
}

.contact_item img {
    width: 40px;
    height: 40px;
}

.contact_item h3 {
    font-weight: 700;
    font-size: 18px;
    text-align: center;
}
.contact_item p {
    text-align: center;
    line-height: 120%;
}

.contact_item a {
    text-align: center;
    border-bottom: 1px dashed #0073c4;
}

.cta {
    background-color: #ceebff;
    border-radius: 15px;
    padding: 50px 20px 20px 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.cta_form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.cta h2 {
    font-size: 32px;
    font-weight: 800;
}

.cta form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.cta form input {
    border: none;
    outline: none;
    border-radius: 15px;
    font-size: 18px;
    padding: 15px;
    background-color: white;
    transition: transform 0.3s ease-in;
    color: #0e1147;
}

.cta form input::placeholder {
    color: #b2d0eb;
}

.cta form input:hover {
    transform: scale(1.025);
}

.cta form textarea {
    font-family: 'Rubik', sans-serif;
    border: none;
    outline: none;
    font-size: 18px;
    padding: 15px;
    border-radius: 15px;
    min-height: 200px;
    resize: none;
    color: #0e1147;
}

.cta form textarea::placeholder {
    color: #b2d0eb;
}

.cta form button {
    cursor: pointer;
    padding: 15px 30px;
    background-color: #9edcff;
    border: none;
    border-radius: 30px;
    font-size: 20px;
    font-weight: 600;
    margin-right: auto;
    color: #0073c4;
    transition: transform 0.3s ease-in;
}

.cta form button:hover {
    transform: scale(1.05);
}

.map iframe {
    border-radius: 15px;
    width: 100%;
    height: 100%;
    border: none;
}

@media (max-width: 900px) {
    .contact_info {
        gap: 10px;
    }
    .contact_item {
        padding: 15px 10px;
    }
    .contact_item img {
        width: 35px;
        height: 35px;
    }
    .cta {
        display: flex;
        flex-direction: column;
    }
    .map iframe {
        height: 350px;
    }
}

@media (max-width: 800px) {
    .contact_info {
        flex-wrap: wrap;
        transform: translateY(8%);
        gap: 15px;
    }
    .contact_item {
        padding: 20px;
    }
    .contact_item:hover {
        transform: scale(1.02);
    }
}

@media (max-width: 520px) {
    .cta h2 {
        text-align: center;
        font-size: 30px;
    }
    .contact_info {
        transform: translateY(4%);
    }
    .contact_item {
        width: 100%;
        height: 175px;
    }
    .cta form button {
        margin: 0 auto;
    }
}
